@font-face {
  font-family: "Avenir";
  src: url(../../assests/fonts/Avenir-Light.woff);
}
@font-face {
  font-family: "AvenirBold";
  src: url(../../assests/fonts/Avenir-Heavy.woff);
}
body {
  font-family: "MyriadProRegular";
  font-size: 12px;
}
a {
  color: #01a6d0;
}
.trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}

.trigger:hover {
  color: #01a6d0;
}

.layoutlogo {
  height: 65px;
  background-color: #01a6d0;
  padding: 10px;
  display: flex;
}
.layoutlogo img {
  width: 40px;
}

.site-layout .site-layout-background {
  background: #fff;
}
.sidebar svg {
  fill: #000 !important;
}
.ant-layout.ant-layout-has-sider {
  min-height: 100vh;
}

.ant-table-pagination-right {
  text-align: center;
  float: none;
  justify-content: center;
}
.ant-table-pagination.ant-pagination {
  margin: 70px 0;
}
