.search .ant-input-affix-wrapper {
  height: 30px !important;
}
.ant-btn-primary {
  border-radius: 4px;
  background-color: #01a6d0;
  border-color: #01a6d0;
  font-family: "MyriadProRegular";
  font-size: 13px;
}
.ModalLook {
  width: 900px !important;
  top: 20px;
}
.ant-menu-inline-collapsed > .ant-menu-item span {
  opacity: 0;
}

.ant-input-search-icon::before {
  border-left: none;
}
.ant-modal-title {
  font-family: "MyriadProBold" !important;
}
.ant-modal-body {
  padding: 0px !important;
  font-size: 10px;
}
.ant-form-item-has-error .ant-form-item-explain,
.ant-form-item-has-error .ant-form-item-split {
  font-size: 10px;
}
.anticon-menu-fold svg {
  fill: #2f2f2f !important;
}
.ant-modal-header {
  padding: 30px 30px 4px 30px;
  background: linear-gradient(to top, #fff, #fff 90%, #949494 100%, #fff 100%);
}
.SideMenu .ant-menu-item {
  padding-left: 20px !important;
  display: flex;
}
.SideMenu .ant-menu-item span {
  /* padding: 5px 10px; */
  padding-left: 10px;
}
.ant-menu-inline-collapsed
  > .ant-menu-submenu
  > .ant-menu-submenu-title
  > .profileMenu {
  opacity: 0;
}
.ant-menu-submenu-title {
  font-size: 12px !important;
}
