.ant-layout-sider-collapsed .proTitle,
.ant-layout-sider-collapsed .layoutlogo span {
  display: none;
  transition: 2s;
  white-space: nowrap;
}
/* tr.ant-table-expanded-row > td,
tr.ant-table-expanded-row:hover > td {
  background-color: #f4f4f4 !important;
} */
.cancelBtn {
  background: #fff;
  color: #01a6d0;
  border: 1px solid #01a6d0;
}
.cancelBtn:hover {
  background: #01a6d0;
}
.ant-btn-primary:hover,
.ant-btn-primary:focus {
  background-color: #0586a6;
  border-color: #0586a6;
  /* background-color: ; */
}
.userTitle {
  color: #37c1d6;
  font-size: 12px;
}
@media (max-width: 576px) {
  .ant-layout-sider-dark .proTitle,
  .ant-layout-sider-dark .layoutlogo span {
    display: none;
    transition: 2s;
  }
  .ant-layout-sider-dark .ant-menu-item span {
    display: none;
  }
}
.ant-table-column-sorters {
  padding: 0px;
}
.ant-table-cell-fix-right-first::after {
  content: none;
}
.filterLabel {
  font-size: 12px;
  font-weight: 600;
  color: #292b2c;
}
.timeLable {
}

.svgicon {
  filter: brightness(200%) contrast(100%) grayscale(100%) invert(7%)
    opacity(25%) saturate(200%) sepia(7%);
  width: 30px;
  height: 30px;
  padding-top: 5px;
}
.ant-menu.ant-menu-dark .ant-menu-item-selected .svgicon {
  filter: brightness(200%) contrast(200%) grayscale(100%) invert(7%)
    saturate(200%) sepia(7%);
}
.ant-menu.ant-menu-dark .ant-menu-item-active .svgicon {
  filter: brightness(200%) contrast(200%) grayscale(100%) invert(7%)
    saturate(200%) sepia(7%);
}
