.reportcontainer {
  /* box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.19); */
  background-color: #ffffff;
  /* margin: 0 16px; */
  padding: 10px;
}
.reporttitle {
  font-size: 16px;
  color: #404040;
  font-weight: 600;
  margin-bottom: 0px;
}
.reportTable {
  /* margin: 30px 6px; */
  /* box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.19); */
  background-color: #ffffff;
  padding: 10px;
}
.reportTable .ant-table-tbody > tr > td {
  text-align: left;
}

.reportTable .ant-table-thead > tr > th {
  text-align: left;
}
.report
  .ant-select-single:not(.ant-select-customize-input)
  .ant-select-selector {
  border-radius: 4px !important;
  height: 30px;
  border: solid 1px #cecece;
  background-color: #fafafa;
}
.report .ant-select {
  font-size: 12px;
  color: #50575a;
  font-weight: 600;
}
.report table tr th.ant-table-selection-column,
.report table tr td.ant-table-selection-column {
  width: 40px;
  min-width: 40px;
  text-align: center;
}
.report .ant-picker {
  border-radius: 4px !important;
  height: 30px;
  border: solid 1px #cecece;
  background-color: #fafafa;
  font-size: 12px;
  color: #50575a;
  font-weight: 600;
}
.report .ant-btn-primary {
  border-radius: 4px !important;
  height: 30px;
  font-size: 12px;
  font-weight: 600;
}
.report .outlineBtn {
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  background-color: #ffffff;
  color: #01a6d0;
  font-size: 13px;
}
.loader {
  text-align: center;
}
.ModalLook.reportModal {
  width: 1000px !important;
}
.tripRegister {
  margin: 0 15px 2px 15px;
}
.tripRegister .ant-table-tbody > tr > td {
  padding: 10px 8px;
  text-align: left;
  color: #191919;
}
.tripRegister .ant-table-thead > tr > th {
  text-align: left;
  padding: 10px 4px;
  background: #45495b;
  color: white;
}
.totalAmountWrapper {
  flex-direction: column;
  padding: 0px 10px;

  display: flex;
}
.totalAmountWrapper .doubleUnderline {
  border-top: 1px solid red;
  padding: 1px;
  border-bottom: 1px solid red;
  width: 50px;
  text-align: center;
}
.advanceTables .ant-table-tbody > tr > td {
  font-size: 13px;
  font-family: "MyriadProRegular";
}
.advanceTables .ant-table-thead > tr > th {
  background-color: #f8f8f8 !important;
  color: rgba(0, 0, 0, 0.85) !important;
  font-family: "MyriadProSemiBold";
  font-size: 13px;
}
.advanceTables h3 {
  font-size: 16px;
  font-weight: 600;
  margin: 30px 0 10px 20px;
  color: #404040;
}
.advanceTables {
  margin: 10px;
}
.line {
  border-top: 1px dashed rgba(0, 0, 0, 0.19);
  border-bottom-color: #fff;
  margin: 30px 0 0 0;
}
.tableSummary {
  background-color: #f0f4f6;
  margin: 0 20px 30px 20px;
  font-size: 14px;
  color: #0088aa;
  font-weight: 600;
}
.tableSummary td {
  padding: 12px 8px !important;
}
.advancetotal {
  background-color: #f0f4f6;
  margin: 0 20px 30px 20px;
  font-size: 14px;

  color: #0088aa;
  font-weight: 600;
  padding: 8px;
}
.triptotalcolone {
  width: 39%;
}
.triptotalcoltwo {
  width: 7%;
  text-align: center;
}
.triptotalcolthree {
  width: 6%;
}
.triptotalcolfour {
  width: 8%;
  text-align: center;
}
.triptotalcolfive {
  width: 10%;
  text-align: center;
}
.triptotalcolsix {
  width: 6%;
  text-align: center;
}
.triptotalcolseven {
  width: 8%;
  text-align: center;
}
.triptotalcoleight {
  width: 8%;
  text-align: center;
}
.advancetotalcolnine {
  width: 8%;
  text-align: center;
}
.advancetotalcolone {
  width: 50%;
  text-align: center;
}
.Vouchertotalcolone {
  width: 25%;
  text-align: left;
}
.Vouchertotalcoltwo {
  width: 55%;
}
.Vouchertotalcolthree {
  width: 20%;
  text-align: left;
  padding: 0 8px;
}
.expensescolone {
  width: 70%;
  text-align: left;
  padding: 0px 14px;
}
.expensescoltwo {
  width: 30%;
  text-align: center;
}
.expenses {
  margin: 0 15px 2px 15px;
}

.expenses .ant-table-tbody > tr > td {
  padding: 10px 20px;
  font-size: 13px;
  font-family: "MyriadProRegular";
  color: #191919;
}
.expenses .ant-table-thead > tr > th {
  padding: 10px 20px;
  font-family: "MyriadProSemiBold";
  font-size: 13px;
}
.cards {
  display: flex;
  margin: 20px 0px;
}
.cardtwo {
  display: flex;
}
.bankDetailsCard h3 {
  font-size: 16px;
  font-weight: 600;

  color: #404040;
}
.bankDetailsCard {
  margin: 20px 30px 10px 30px;
}
.cardone .ant-card {
  border-radius: 12px;
  margin-right: 30px;
  box-shadow: 0 2px 1px 0 rgba(63, 63, 63, 0.16);
  border: solid 1px #f1f1f1;
  background-image: linear-gradient(to right, #ffffff 9%, #f0f4f6 90%);
}
.cardtwo .ant-card {
  border-radius: 12px;
  border: 1px dashed #dedfe1;
  background-color: #f6f7fa;
}

.cards .ant-card-body {
  padding: 12px 24px 0 24px;
}
.cardRow {
  display: flex;
  margin: 0 0 20px 0;
}
.cardtitle {
  font-size: 12px;
  margin-bottom: 0;
  color: rgba(76, 76, 76, 0.85);
}
.cardpara {
  font-size: 12px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.85);
}
.cardphone {
  font-size: 12px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.85);
  padding-left: 16px;
}
.reportdetails .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  padding: 5px;
  font-family: "MyriadProSemiBold";
}
.reportdetails .ant-table-tbody > tr > td {
  padding: 6px;
  text-transform: capitalize;
  font-size: 13px;
  font-family: "MyriadProRegular";
  color: #191919;
}
.totalAmountadvance {
  flex-direction: column;
  padding: 0;
  display: flex;
}
