.consignmentList .ant-menu {
  background: transparent !important;
  margin-left: 13px;
}
.consignmentList .ant-menu ul {
  list-style: none;
  margin-left: 0px;
  padding: 0px;
  padding-top: 30px;
  background: #000;
  padding-bottom: 10px !important;
}
.consignmentList .ant-menu-item {
  padding-left: 0px !important;
  padding-right: 0px !important;
}
.consignmentList .ant-menu li {
  display: inline;
  /* padding-right: 20px; */
  font-size: 13px !important;
  font-family: "MyriadProSemiBold";
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #404040;
  margin: 0 20px !important;
}
.consignmentList .ant-menu li:nth-child(2) {
  margin-left: 10px !important;
}
.consigmentActiveItem:hover {
  border-bottom: 4px solid #ffc502 !important;
}

.consigmentActiveItem {
  border-bottom: 4px solid #ffc502 !important;
  color: inherit !important;
  font-size: 14px !important;
  padding-bottom: 10px !important;
}
.consigmentItem {
  color: inherit !important;
  font-size: 14px !important;
  font-weight: normal !important;
  font-stretch: normal !important;
  font-style: normal !important;
  line-height: normal !important;
  letter-spacing: normal !important;
  color: #404040 !important;
  padding-bottom: 10px !important;
  border-bottom: 4px solid transparent !important;
}

.consigmentItem:hover {
  border-bottom: 4px solid #ffc502 !important;
  color: inherit !important;
  font-size: 14px !important;
  font-weight: bold !important;
  font-stretch: normal !important;
  font-style: normal !important;
  line-height: normal !important;
  letter-spacing: normal !important;
  color: #0053a0 !important;
  padding-bottom: 10px !important;
}
.consigmentItem:active {
  background: #000;
}
