.button {
  border-radius: 4px;
  border: solid 1px #d9d9d9;
  background-color: #ffffff;
  margin: 0px 10px;
  color: #01a6d0;
  font-size: 13px;
  font-weight: 400;
  font-family: "MyriadProRegular";
}
.addbtn {
  margin: 0px 10px;
}
.searchbtn {
  border-radius: 4px;
  border: solid 1px #cecece;
  background-color: #fafafa;
}
.searchbtn .ant-input {
  background-color: #fafafa;
}

.maintable .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  /* line-height: 1.38; */
  padding: 5px;
  font-family: "MyriadProSemiBold";
}
.maintable .ant-table-tbody > tr > td {
  padding: 6px;
  text-transform: capitalize;
  font-size: 13px;
  font-family: "MyriadProRegular";
  color: #191919;
}
.maintable .ant-table-tbody > tr.ant-table-row-selected:hover > td {
  background: #e6f7ff;
}
.insideTable .ant-table-thead > tr > th {
  background: #7ae4ff !important;
  color: rgba(0, 0, 0, 0.85) !important;
  font-family: "MyriadProSemiBold";
  padding: 5px;
  font-size: 11px;
  padding: 5px;
}
.insideTable .ant-table-body > tr > td {
  font-family: "MyriadProRegular";
  padding: 5px;
  font-size: 13px;
  padding: 5px;
  color: #191919;
}

.insideTable .ant-table {
  border: 1px solid #e8e8e8;
  margin: 10px !important;
}
.ant-table-tbody > tr.ant-table-row-level-1:hover > td {
  background: #000;
}
.ownerTableHeader .ant-table {
  position: relative;
}
