.ant-table.ant-table-bordered thead > tr > th,
.ant-table.ant-table-bordered tbody > tr > td,
.ant-table.ant-table-bordered tfoot > tr > th,
.ant-table.ant-table-bordered tfoot > tr > td {
  border-right: 0px !important;
}
.antPdfModal {
  width: 900px !important;
  top: 50px !important;
}
.antImageModal {
  width: 900px !important;
  top: 50px !important;
}
.antImageModal .ant-modal-body,
.antPdfModal .ant-modal-body {
  display: flex !important;
  justify-content: center !important;
}
.recieveregTable {
  overflow-x: auto;
  margin: 10px 10px 0px 10px;
}
.recieveregTable .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  /* line-height: 1.38; */
  padding: 5px;
  font-family: "MyriadProRegular";
}
.recieveregTable .ant-table-tbody > tr > td {
  padding: 7px;
  text-transform: capitalize;
  font-size: 13px;
  font-family: "MyriadProRegular";
  color: #191919;
}
.recieveregTable .ant-table-tbody > tr.ant-table-row-selected:hover > td {
  background: #cff5ff;
}
.recieveregTable .ant-table-cell-fix-right {
  box-shadow: -2px 0 2px 0 rgba(0, 0, 0, 0.24);
}
.svgediticon {
  filter: brightness(200%) contrast(100%) grayscale(100%) invert(7%)
    opacity(90%) saturate(200%) sepia(7%);
  width: 30px;
  height: 30px;
  padding-top: 5px;
}
