.title {
  color: #292b2c;
  font-size: 12px;
  font-weight: 600;
}
.recoverytitle {
  font-size: 18px;
  font-family: "MyriadProBold";
  margin: 0px;
}
.recovertPara {
  font-size: 12px;
  font-family: "MyriadProBold";
}
.modalTitle {
  font-size: 20px;
  margin: 0px;
}
.modalPara {
  font-size: 12px;
  font-family: "MyriadProBold";
}
.rmoveBtn_a{
  background-color: #fff;
  color: #01a6d0;
  border: 1px solid #01a6d0;
  height: 20px;
  padding: 1px;
  width: 40px;
  font-size: 12px;
  font-family: "MyriadProRegular";
}
.rmoveBtn {
  background-color: #fff;
  color: #01a6d0;
  border: 1px solid #01a6d0;
  height: 20px;
  padding: 1px;
  width: 64px;
  font-size: 12px;
  font-family: "MyriadProRegular";
}
.addBtn {
  background-color: #fff;
  color: #01a6d0;
  border: 1px solid #01a6d0;
  height: 20px;
  width: 30px;
  font-size: 12px;
  padding: 1px;
  width: 42px;
  font-family: "MyriadProRegular";
}
.recoveryTable {
  margin: 10px;
  overflow-x: auto;
}

.delivery-number-drag-area {
  min-width: 320px;
}

.recoveryTable .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  /* line-height: 1.38; */
  padding: 5px;
  font-family: "MyriadProSemiBold";
}
.recoveryTable .ant-table-tbody > tr > td {
  padding: 6px;
  text-transform: capitalize;
  font-size: 13px;
  font-family: "MyriadProRegular";
  color: #191919;
}
.recoveryTable table {
  table-layout: auto !important;
  width: 100%;
}

.recoveryInsideTable .ant-table-thead > tr > th {
  background: #e8e8e8 !important;
  color: rgba(17, 11, 11, 0.85) !important;
  font-family: "MyriadProSemiBold";
  padding: 5px;
  font-size: 12px;
  padding: 5px;
}
.recoveryInsideTable .ant-table-body > tr > td {
  font-family: "MyriadProRegular";
  padding: 5px;
  font-size: 13px;
  padding: 5px;
  color: #191919;
}

.recoveryInsideTable .ant-table {
  border: 1px solid #e8e8e8;
  margin: 10px 40px !important;
}

.recoveryInsideTable .ant-table-tbody > tr > td {
  font-size: 12px;
}
.recoveryTable tr.ant-table-expanded-row > td,
tr.ant-table-expanded-row:hover > td {
  background: #f1fcff;
}

.saveBtn {
  background-color: #fff;
  color: #01a6d0;
  border: 1px solid #01a6d0;
  height: 20px;
  padding: 1px;
  width: 40px;
  font-size: 12px;
  margin: 0;
  font-family: "MyriadProRegular";
}
.recoveryTable .editable-cell-value-wrap {
  padding: 6px;
  height: 32px;
  border-radius: 2px;
  /* border: solid 1px #cecece; */
}

.activeRowSaveBtn {
  background-color: #01a6d0;
  color: #fff;
}

.recoveryTable .ant-table tfoot > tr > td {
  padding: 10px 16px 7px 5px;
  font-size: 13px;
  font-family: "MyriadProSemiBold";
  font-size: 16px;
  border: 0px;
}
.recoveryTable .monthlysummary {
  color: #0088aa;
  background-color: #f0f4f6;
  font-weight: bold;
}
.recoveryTable .monthlysummaryred {
  color: #d82c1f;
  background-color: #f0f4f6;
}
/* .digitValue {
  text-align: right;
  padding-right: 80px;
} */
.recoveryTable .editable-cell {
  position: relative;
}

.recoveryTable .editable-cell-value-wrap {
  padding: 5px 12px;
  cursor: pointer;
}

.recoveryTable .editable-cell-value-wrap:hover {
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  padding: 4px 11px;
}

[data-theme="dark"] .editable-row:hover .editable-cell-value-wrap {
  border: 1px solid #434343;
}

.recoveryInsideTable .editable-cell-value-wrap {
  padding: 6px;
  height: 32px;
  border-radius: 2px;
  border: solid 1px #cecece;
}
.recoveryTable .ant-form-item-explain {
  display: none;
  font-size: 8px;
}
.show-help-leave {
  font-size: 8px;
  padding: 0px;
}
.show-help-leave-active {
  font-size: 8px;
  padding: 0px;
}
.show-help {
  padding: 0px;
  font-size: 8px;
  margin: 0px;
}
.bgfb .ant-card-body {
  padding: 22px;
}
.brd .ant-card {
  background-color: #f0f4f6;
  font-size: 16px;
  height: 42px;
}
.brdCol-1 {
  width: 10%;
}
.brdCol-2 {
  width: 20%;
}
.brdCol-3 {
  width: 11%;
  /* text-align: right; */
}
.brdCol-4 {
  width: 15%;
  /* text-align: right; */
}
.brdCol-5 {
  width: 11%;
  text-align: right;
}
.brdCol-6 {
  width: 4%;
}
.insiderecoveryFooter .ant-table-thead > tr > th {
  display: none;
}
.insiderecoveryFooter .ant-table-tbody > tr > td {
  color: #0088aa;
  background-color: #f0f4f6;
  font-weight: bold;
  font-size: 16px;
}
.insiderecoveryFooter {
  margin: 20px 0px 20px 0px;
}
.insiderecoveryFooter .ant-empty {
  display: none;
}
.demo-infinite-container {
  border: 1px solid #e8e8e8;
  border-radius: 4px;
  overflow: auto;
  padding: 8px 24px;
  height: 270px;
}
.demo-loading-container {
  position: absolute;
  bottom: 40px;
  width: 100%;
  text-align: center;
}
[data-theme="dark"] .demo-infinite-container {
  border: 1px solid #303030;
}
