@font-face {
  font-family: "MyriadProRegular";
  font-style: normal;
  font-stretch: normal;
  font-weight: normal;
  src: local("MyriadProRegular"),
    url(./assests/fonts/MyriadProRegularFont.woff) format("woff");
}
@font-face {
  font-family: "MyriadProBold";
  font-style: normal;
  font-stretch: normal;
  font-weight: normal;
  src: local("MyriadProBold"),
    url(./assests/fonts/MyriadProBoldFont.woff) format("woff");
}
@font-face {
  font-family: "MyriadProSemiBold";
  font-style: normal;
  font-stretch: normal;
  font-weight: normal;
  src: local("MyriadProSemiBold"),
    url(./assests/fonts/MyriadProSemiBold.woff) format("woff");
}
@font-face {
  font-family: "MyriadProSemiBoldItalic";
  font-style: normal;
  font-stretch: normal;
  font-weight: normal;
  src: local("MyriadProSemiBoldItalic"),
    url(./assests/fonts/MyriadProSemiBoldItalic.woff) format("woff");
}
@font-face {
  font-family: "MyriadProLight";
  font-style: normal;
  font-stretch: normal;
  font-weight: normal;
  src: local("MyriadProLight"),
    url(./assests/fonts/MyriadProLight.woff) format("woff");
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  color: #191919;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
