.site-card-border-less-wrapper {
  margin: 20px;
}

.custom-tooltip {
  background-color: white;
  border: 1px solid gray;
  padding: 8px;
}

.monthly-earning-typo {
  font-size: 16px;
}

.monthly-earning-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.arrow-wrapper.positive {
  color: #3f8600;
}

