.recive .outlineBtn {
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  background-color: #ffffff;
  color: #01a6d0;
  font-size: 13px;
}
.recive
  .ant-select-single:not(.ant-select-customize-input)
  .ant-select-selector {
  border-radius: 4px !important;
}
.recive .ant-picker,
.recive .ant-input {
  border-radius: 4px !important;
}
.recive .ant-picker {
  height: 30px;
}
.recive .ant-input-affix-wrapper {
  max-height: 30px !important;
  height: 30px;
}
.recive .ant-select-selector {
  height: 30px;
}
.challansearch .ant-input {
  height: 23px;
}
.postBtn {
  background: #fff;
  border: 1px solid #01a6d0;
  background: #01a6d0;
  color: #fff;
}
.postBtn:hover {
  background: #fff;
  color: #01a6d0;
  border: 1px solid #01a6d0;
}
.truckmaster .ant-picker,
.truckmaster .ant-input {
  width: 80px;
  height: 30px;
  font-size: 12px;
}
.truckmaster .ant-picker-input {
  height: 20px;
  width: 80px;
}
.recieveackTable .ant-input {
  /* height: 20px; */
  width: 80px;
}
.truckmaster .ant-form-item-has-error .ant-form-item-explain {
  display: none;
}
.truckmaster .ant-form-item-explain,
.ant-form-item-extra {
  display: none;
}
.truckmaster .ant-form-item {
  margin-bottom: 0px !important;
}
.antPdfModal {
  width: 900px !important;
  top: 50px !important;
}
.antImageModal {
  width: 900px !important;
  top: 50px !important;
}
.antImageModal .ant-modal-body,
.antPdfModal .ant-modal-body {
  display: flex !important;
  justify-content: center !important;
}
.ant-table-cell-fix-left {
  box-shadow: 2px 0 2px 0 rgba(0, 0, 0, 0.24);
}
.recieveackTable .recieveAcknolwedement .ant-picker {
  width: 120px;
}
.recieveackTable .recieveAcknolwedement .ant-picker-input {
  width: 120px;
}
.recieveackTable {
  overflow-x: auto;
  margin: 10px 10px 0px 10px;
}
.recieveackTable .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  /* line-height: 1.38; */
  padding: 5px;
  font-family: "MyriadProRegular";
}
.recieveackTable .ant-table-tbody > tr > td {
  padding: 7px;
  text-transform: capitalize;
  font-size: 13px;
  font-family: "MyriadProRegular";
  color: #191919;
}
.recieveackTable .ant-table-tbody > tr.ant-table-row-selected:hover > td {
  background: #cff5ff;
}
.recieveackTable .ant-table-cell-fix-right {
  box-shadow: -2px 0 2px 0 rgba(0, 0, 0, 0.24);
}
