.button {
  border-radius: 4px;
  border: solid 1px #d9d9d9;
  background-color: #ffffff;
  margin: 0px 10px;
  color: #01a6d0;
  font-size: 14px;
  font-weight: 400;
  font-family: "MyriadProRegular";
}
.addbtn {
  margin: 0px 10px;
}
.useronboarding .searchbtn {
  border-radius: 4px;
  border: solid 1px #cecece;
  background-color: #fafafa;
  height: 30px;
}
.searchbtn .ant-input {
  background-color: #fafafa;
}
.useronboardingtable {
  margin: 10px;
}
.useronboardingtable .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  padding: 10px;
  font-family: "MyriadProRegular";
}
.useronboardingtable .ant-table-tbody > tr > td {
  padding: 10px;
  font-size: 13px;
  color: #191919;
  font-family: "MyriadProRegular";
}
.useronboardingtable .ant-table-tbody > tr.ant-table-row-selected:hover > td {
  background: #cff5ff;
}
.useronboarding .ant-switch-checked {
  background-color: #01a6d0;
}
.useronboarding .toggleBtn {
  border: 0px;
  background: none;
  padding: 0px;
  height: 19px;
  margin: 1px 0px;
}
