.vehicleinfoForm .ant-col {
  padding: 0px 5px 10px 5px;
}
.vehicleinfoForm h3 {
  font-family: "AvenirMedium";
  font-size: 12px;
  font-weight: 600;
  margin: 0px;
}
.require::after {
  display: inline-block;
  margin-right: 4px;
  color: #ff4d4f;
  font-size: 14px;
  font-family: "Avenir";
  line-height: 1;
  content: "*";
}
.vehicleinfoForm h2 {
  font-family: "AvenirMedium";
  font-size: 18px;
  font-weight: 600;
}

.vehicleinfoForm .ant-form-item {
  margin-bottom: 6px !important;
}
.vehicleinfoForm .ant-modal-title {
  font-family: "AvenirBold";
}
.vehicleinfoForm .ant-input {
  height: 28px !important;
  border-radius: 2px;
  border: solid 1px #cecece;
  margin-top: 2px;
  width: 100%;
  font-size: 12px;
}
.ownerform .ant-input {
  width: 94%;
}
.bankform .ant-input {
  width: 94%;
}

.vehicleinfoForm .ant-select-selector {
  height: 28px !important;
}
.vehicleinfoForm .ant-select-selection-item {
  font-size: 12px;
  line-height: 25px !important;
}
.cancelBtn {
  background: #fff;
  color: #01a6d0;
  border: 1px solid #01a6d0;
}
.cancelBtn:hover {
  background: #01a6d0;
}
.ant-btn-primary {
  font-family: "AvenirMedium";
  font-size: 13px;
}
.ant-input-number-input {
  height: 26px !important;
}
.ant-input-number {
  width: 100% !important;
}
.vehicleinfoForm .ant-input[disabled] {
  color: #7a7a7a;
  font-size: 12px;
}
.vehicleinfoForm .ant-upload.ant-upload-drag .ant-upload-btn {
  height: 102px;
}
.savebtn {
  width: 80px;
  margin-left: 10px;
}
