  ont-size: 13px;
  font-family: myriadprosemibold;
  color: #191919;
}
.settings .userdesc {
  text-transform: capitalize;
  font-size: 13px;
  font-family: myriadproregular;
  color: #191919;
}
.editsettings .ant-col {
  padding: 0px 5px 10px 5px;
}
