.expandparent {
  background-color: #e6f7ff !important;
}
/* .expandparent:hover {
  background-color: #e6f7ff !important;
} */
.antPdfModal {
  width: 900px !important;
  top: 50px !important;
}
.antImageModal {
  width: 900px !important;
  top: 50px !important;
}
.antImageModal .ant-modal-body,
.antPdfModal .ant-modal-body {
  display: flex !important;
  justify-content: center !important;
}
.antPdfModal .ant-modal-title,
.antImageModal .ant-modal-title {
  font-weight: bold !important;
  text-align: center !important;
  font-size: 20px !important;
}
.pdfView p {
  display: none !important;
}
.truckmaster td.ant-table-column-sort {
  background: transparent;
}
.truckmaster {
  overflow-x: auto;
  margin: 10px 10px 0px 10px;
}
.trucktable .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  /* line-height: 1.38; */
  padding: 5px;
  font-family: "MyriadProRegular";
}
.trucktable .ant-table-tbody > tr > td {
  padding: 7px;
  text-transform: capitalize;
  font-size: 13px;
  font-family: "MyriadProRegular";
  color: #191919;
}
.trucktable .ant-table-tbody > tr.ant-table-row-selected:hover > td {
  background: #cff5ff;
}
.ant-select-item-option-active:not(.ant-select-item-option-disabled) {
  background: #d6f7ff;
}
