.dispatchChallan .outlineBtn {
  border-radius: 4px;
  border: 1px solid #d9d9d9;
  background-color: #ffffff;
  color: #01a6d0;
  font-size: 13px;
}
.dispatchChallan
  .ant-select-single:not(.ant-select-customize-input)
  .ant-select-selector {
  border-radius: 4px !important;
}
.dispatchChallan .ant-picker,
.dispatchChallan .ant-input {
  border-radius: 4px !important;
}
.dispatchChallan .ant-picker {
  height: 30px;
  background-color: #fafafa;
}
.dispatchChallan .ant-select-multiple .ant-select-selector {
  border-radius: 4px;
  background-color: #fafafa;
}
.dispatchChallan .ant-input-affix-wrapper {
  max-height: 30px !important;
  height: 30px;
  width: 180px;
}
.dispatchChallan .ant-select-selector {
  height: 30px;
}
.dispatchChallan .ant-select-selection-placeholder {
  font-size: 12px;
}

.dispatchChallanTable {
  overflow-x: auto;
  margin: 10px 10px 0px 10px;
}
.dispatchChallanTable .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  /* line-height: 1.38; */
  padding: 5px;
  font-family: "MyriadProRegular";
}
.dispatchChallanTable .ant-table-tbody > tr > td {
  padding: 7px;
  text-transform: capitalize;
  font-size: 13px;
  font-family: "MyriadProRegular";
  color: #191919;
}
.dispatchChallanTable .ant-table-tbody > tr.ant-table-row-selected:hover > td {
  background: #cff5ff;
}
