.voucherbook {
  overflow-x: auto;
  margin: 10px 10px 0px 10px;
}
.voucherbook .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  /* line-height: 1.38; */
  padding: 5px;
  font-family: "MyriadProRegular";
}
.voucherbook .ant-table-tbody > tr > td {
  padding: 7px;
  text-transform: capitalize;
  font-size: 13px;
  color: #191919;
  font-family: "MyriadProRegular";
}
.vehicleinfoForm .ant-picker {
  height: 28px;
  margin: 3px 0;
}
.vehicleinfoForm .ant-picker-input > input {
  font-size: 12px;
}
.voucherbook .ant-table tfoot > tr > td {
  padding: 10px 16px 7px 5px;
  font-size: 13px;
  font-family: "MyriadProSemiBold";
  font-size: 16px;
  border: 0px;
}
.voucherbook .monthlysummary {
  color: #0088aa;
  background-color: #f0f4f6;
}
