@font-face {
  font-family: "Avenir";
  src: url(../../assests/fonts/Avenir-Light.woff);
}
@font-face {
  font-family: "AvenirBold";
  src: url(../../assests/fonts/Avenir-Heavy.woff);
}
@font-face {
  font-family: "AvenirBook";
  src: url(../../assests/fonts/Avenir-Book.woff);
}
@font-face {
  font-family: "AvenirBlack";
  src: url(../../assests/fonts/Avenir-Black.woff);
}
@font-face {
  font-family: "AvenirMedium";
  src: url(../../assests/fonts/Avenir-Medium.woff);
}
@font-face {
  font-family: "AvenirRoman";
  src: url(../../assests/fonts/Avenir-Roman.woff);
}
.loginarea {
  width: 100;
  min-height: 100vh;
  background-color: #f2f2f2;
  overflow: none;
  text-align: center;
}
.leftloginarea {
  background: #00c6f8;
  height: 100vh;
}
.loginbg {
  top: 20%;
  position: absolute;
  left: 30%;
}

.headtitle {
  font-size: 50px;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: normal;
  line-height: normal;
  font-family: "AvenirBold";
  color: #ffffff !important;
}
.headdesc {
  font-size: 50px;
  font-stretch: normal;
  letter-spacing: normal;
  color: #ffffff !important;
  line-height: 0px;
  margin-top: 60px;

  font-family: "Avenir";
}
.loginfooter {
  left: 50px;
  bottom: 30px;
  width: 100%;
  color: white;
  text-align: left;
  position: absolute;
}
.Rightloginarea {
  width: 412px;
  margin: 10vh auto;
}
.rightlogintxtarea {
  padding-top: 50px;
}
.rightlogintitle {
  font-size: 20px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #3b3b3b;
}
.rightlogindesc {
  font-size: 16px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #5a6067;
}
.loginForm {
  margin: 2% 10%;
  text-align: left;
}
.loginuserfield {
  height: 40px;
  border-radius: 4px !important;
}
.ant-input-affix-wrapper {
  max-height: 40px !important;
}
.loginForm .anticon svg {
  fill: #0154cc;
}
.loginbtn {
  border-radius: 4px !important;
  background-color: #01a6d0 !important;
  border-color: #01a6d0 !important;
  height: 40px !important;
  width: 100%;
  color: #fff !important;
  font-size: 18px !important;
  font-weight: 600 !important;
}
.logo img {
  width: 200px;
}
.rightloginfooter {
  /* right: 50px;
  bottom: 30px;
  width: 100%; */
  color: white;
  text-align: right;
  bottom: 30px;
  right: 150px;
  position: absolute;
  color: #002145;
}
.rightfooter {
  display: none;
}
.errorform {
  margin-bottom: 5px;
}
@media (max-width: 768px) {
  .leftloginarea {
    background: #00c6f8;
    height: 320px;
    display: flex;
  }
  .rightloginfooter {
    display: none;
  }

  .loginbg {
    position: absolute;
    left: 10%;
    display: flex;
  }
  .leftitlearea {
    margin: 20px;
  }
  .headtitle {
    margin: 20px 0px 0px 40px;
  }
  .headdesc {
    margin: 30px 0px 0px 40px;
  }
  .loginfooter {
    display: none;
  }
  .Rightloginarea {
    width: 500px;
    margin: 5vh auto;
  }

  .rightfooter {
    display: block;
    position: fixed;
    bottom: 60px;
    left: 40%;
  }
}
@media (max-width: 576px) {
  .leftloginarea {
    display: none;
  }
  .loginarea {
    background: #fff !important;
  }
  .Rightloginarea {
    width: 100%;
    background: #000;
    left: 0;
    right: 0;
    margin: 2px;
    border-radius: 7px;
    background-color: #f2f2f2;
    overflow: auto;
    margin: auto;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }
  .rightloginfooter {
    display: none;
  }
  .rightfooter {
    display: block;
    position: fixed;
    bottom: 60px;
    text-align: center;
    left: 25%;
  }
  .logo {
    margin-top: 10%;
  }
}
.ant-btn-primary {
  border-radius: 4px;
  background-color: #01a6d0;
  border-color: #01a6d0;
  font-family: "MyriadProRegular";
  font-size: 13px;
}
