.cashBookContainer {
  padding: 15px 68px;
}
.cashBookHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.cashBookContainer h2 {
  font-size: 16px;

  color: #404040;
  font-weight: 600;
  margin-bottom: 0px;
  margin-top: 16px;
}
.addEntry {
  height: 30px;
}
.cashBookTable {
  /* max-width: 990px; */
  margin: auto;
}

.cashBookContainer .ant-form-item-has-error .ant-form-item-explain {
  display: none;
}
.cashBookContainer .show-help {
  display: none;
}

.cashBookContainer .ant-form-item-has-error input,
.cashBookContainer .ant-form-item-has-error .ant-picker {
  border-color: red;
}

.cashBookTable .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: 700;
  font-stretch: normal;
  font-style: normal;
  padding: 5px;
  font-family: myriadprosemibold;
}

.cashBookTable .ant-table-tbody > tr.ant-table-row > td {
  height: 44px;
  font-size: 13px;
  position: relative;
  color: #191919;
  padding: 10px 16px;
  font-family: "MyriadProRegular";
}

.cashBookTable .ant-table-tbody > tr.ant-table-row > td:nth-child(4),
.cashBookTable .ant-table-tbody > tr.ant-table-row > td:nth-child(5) {
  color: #000;
}

.cashBookTable .ant-table-tbody > tr.ant-table-row:hover > td {
  background-color: #d2f6ff;
}

.cashBookTable .ant-table-thead > tr > th:first-child {
  box-sizing: border-box;
  width: 8%;
}

/* experimental */
.cashBookTable .ant-form-item {
  position: absolute;
  top: 5px;
}
.cashBookContainer
  .ant-select-single:not(.ant-select-customize-input)
  .ant-select-selector {
  font-size: 14px;
  color: #7e7e7e;
  height: 30px;
}

.cashBookContainer
  .ant-select-single.ant-select-show-arrow
  .ant-select-selection-placeholder {
  padding-right: 18px;
  font-size: 14px;
  color: #7e7e7e;
  font-weight: 500;
}

.cashBookTable .ant-row.ant-form-item {
  width: 86%;
}
.cashBookTable .ant-picker {
  border: solid 1px #cecece;
  height: 30px;
  padding: 4px 6px 4px;
  font-size: 13px;
}
.cashBookTable .ant-input {
  height: 30px;
  border: solid 1px #cecece;
  padding: 4px 6px;
  font-size: 13px;
}
.cashBookTable .ant-picker-input > input {
  font-size: 13px;
}
.balanceTitle {
  font-size: 16px;
  font-weight: 600;
  color: #0088aa;
}
.balanceRow {
  margin: 20px 0;
  background-color: #f0f4f6;
}

.balanceText {
  font-weight: bold;
  font-family: "MyriadProSemiBold" !important;
  color: #0088aa;
  font-size: 16px;
  padding-left: 6px;
}

.balanceRow > div {
  padding: 10px 9px;
}

.balanceRow > div:last-child {
  padding: 10px 12px;
}

.balanceRow > div:first-child {
  background: #fff;
}

.balanceRow.first > div:not(:first-child) {
  background-color: #fff;
  border-bottom: 3px solid rgba(0, 0, 0, 0.14);
}
.colOne {
  width: 22%;
}
.colTwo {
  width: 33%;
}
.colDebit {
  width: 16%;
}
.colCredit {
  width: 17%;
}
.colOutstanding {
  width: 12%;
}
.cashbookTitle {
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 0px;
  color: #292b2c;
  font-family: "MyriadProRegular";
}
.saveCashbook {
  font-size: 10px;
  height: 25px;
  padding: 3px;
  width: 44px;
}

.cancelCashbook {
  background: #fff !important;
  margin: 0 10px;
  font-size: 10px;
  height: 25px;
  padding: 3px;
  width: 60px;
  color: #01a6d0;
  border: 1px solid #01a6d0;
  font-family: "MyriadProRegular";
}
.cancelCashbook:hover {
  background: #01a6d0 !important;
  border: 1px solid #01a6d0 !important;
}
.cancelCashbook:focus {
  background: #01a6d0 !important;
}
.closeIcon {
  font-size: 18px;
  color: #01a6d0;
  margin: 2px 0px;
  cursor: pointer;
}
.closebtn {
  padding: 0 4px;
}
