.master-data-maindiv {
  border: solid 1px #cecece;
  height: 320px;
}
.typeTitle {
  color: #292b2c;
  font-size: 12px;
  font-weight: 600;
}
.addbtn {
  border-radius: 4px;
  background-color: #01a6d0;
  border-color: #01a6d0;
  font-family: "MyriadProBold";
  font-size: 13px;
  height: 28px;
  margin: 2px 20px;
  color: #ffffff;
  padding: 2px 20px;
}
.saveicon {
  font-size: 18px;
  color: #01a6d0;
  vertical-align: middle;
  cursor: pointer;
}
.addbtn:hover {
  background-color: #0586a6;
  border-color: #0586a6;
  color: #fff;
}
.searchdata {
  border-radius: 4px;
  border: solid 1px #cecece;
  background-color: #ffffff;
  height: 28px;
}
.searchdata .ant-input {
  border-radius: 2px;
  border: solid 1px #cecece;
  margin-top: 2px;
  width: 100%;
  font-size: 12px;
}
.searchdiv {
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.18);
  border-bottom: solid 1px #cecece;
  background-color: #fafafa;
  padding: 10px;
}
.searchdata .ant-input-affix-wrapper {
  height: 30px;
}
.master-data-maindiv .ant-table-thead > tr > th {
  background-color: transparent;
  height: 0;
  padding: 0;
}
.master-data-maindiv .ant-table-tbody > tr > td {
  font-size: 12px;
  font-weight: 500;
  color: #000000;
  padding: 2px 12px;
  height: 40px;
  vertical-align: middle;
}
.materialTable .ant-input {
  height: 26px;
  border: solid 1px #cecece;
  padding: 0px 6px;
  font-size: 12px;
  font-weight: 500;
  color: #000000;
  margin: 0;
}
.materialTable {
  padding: 0 2px;
}
.materialTable .ant-form-item-explain {
  display: none;
}
.materialTable .ant-form-item-has-error .ant-input,
.ant-form-item-has-error .ant-input-affix-wrapper,
.ant-form-item-has-error .ant-input:hover,
.ant-form-item-has-error .ant-input-affix-wrapper:hover {
  background-color: #fff;
  border-color: #ff4d4f;
}
.editbox {
  margin: 0;
}
.material-type .ant-input {
  height: 28px;
}
