.ownerAdvanceTable .ant-table-thead > tr > th {
  background-color: #45495b !important;
  color: #fff !important;
  font-size: 13px;
  font-weight: 700;
  font-stretch: normal;
  font-style: normal;
  padding: 5px;
  font-family: myriadprosemibold;
}
.ownerAdvanceTable .ant-table-tbody > tr > td {
  padding: 8px 10px;
  position: relative;
  color: #191919;
  /* color: rgba(0, 0, 0, 0.85); */
  font-size: 13px;
  font-family: "MyriadProRegular";
}
.cashBookHeader .ant-picker {
  background-color: #fafafa;
  border-radius: 4px;
}
.editablebtn {
  display: flex;
  float: right;
}
.nestedTable .ant-table-thead > tr > th {
  background-color: #7ae4ff !important;
  color: rgba(0, 0, 0, 0.85) !important;
  height: 38px;
  font-size: 13px;
  font-family: "MyriadProRegular";
  font-weight: 600;
  padding: 10px;
}
.nestedTable .ant-picker {
  height: 28px;
}
.nestedTable .ant-input {
  height: 28px;
}
.nestedTable .ant-table-tbody > tr > td {
  padding: 6px 10px;
}

.ownerAdvanceTable tr.ant-table-expanded-row > td {
  background: #e1f9ff7a !important;
}
.nestedTable .ant-table-thead > tr > th:first-child {
  padding: 10px 10px 10px 20px;
}
.nestedTable .ant-table-tbody > tr > td:first-child {
  padding: 6px 10px 6px 20px;
}

.outstanding {
  margin: 20px 0 30px 0;
}
.outstandingColOne {
  width: 23%;
  background-color: #e9eef1;
}
.outstandingColthree {
  width: 20%;
  background-color: #e9eef1;
  font-size: 16px;
  font-weight: 600;
  padding: 10px 15px;
  color: #d82c1f;
}
.outstandingColTwo {
  width: 24%;
  background-color: #e9eef1;
  font-size: 16px;
  font-weight: 600;
  padding: 10px 0;
  color: #d82c1f;
}

.expandparent {
  background-color: #e6f7ff !important;
}
